.chat {
  flex: 0.65;
  display: flex;
  flex-direction: column;
}

.chat_header {
  display: flex;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid lightgray;
}

.chat_headerInfo {
  flex: 1;
  margin-left: 10px;
}

.chat_headerInfo h2 {
  font-size: 16px;
}

.chat_headerInfo p {
  font-size: 12px;
}

.chat_headerIcons {
  display: flex;
  align-items: center;
}

.chat_headerIcons svg {
  font-size: 18px;
}

/* Css of chat body starts */

.chat_body {
  background-color: #000;
  flex: 1;
  background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
  padding: 25px 15px;
  overflow-y: auto;
}

.chat_message {
  position: relative;
  background-color: #fff;
  padding: 5px;
  border-radius: 5px;
  width: fit-content;
  font-size: 14px;
  margin-bottom: 30px;
}

.chat_name {
  position: absolute;
  top: -18px;
  left: 5px;
  font-size: 10px;
  font-weight: bold;
  width: 100%;
}

.chat_time {
  margin-left: 10px;
  font-size: 10px;
}

.chat_receiver {
  background-color: #d6f8c6 !important;
  margin-left: auto;
}

/* Css of chat footer starts */

.chat_footer {
  display: flex;
  align-items: center;
  padding: 10px;
}

.chat_footer form {
  flex: 1;
}

.chat_footer input[type="text"] {
  flex: 1;
  border: 0;
  outline: 0;
  padding: 0 5px;
  border-radius: 5px;
  margin-left: 5px;
  height: 40px;
  width: 100%;
}

.chat_footer input[type="submit"] {
  display: none;
}
